.main
    flex-grow: 1
    display: flex

.promo
    min-height: 960px
    flex-grow: 1
    padding-top: 136px
    position: relative
    overflow: hidden
    @include mdx
        padding-top: 0px
    @include sm 
        padding-top: 184px
    @include xs
        min-height: 0 
        padding-top: 144px
    @include xxs 
        padding-top: 124px
    &__bg-img-top 
        position: absolute
        top: 0
        left: 0
        img
            height: 300px
            width: 100vw
            @include sm
                width: 300%
    &__bg-img-bottom 
        position: absolute
        bottom: 0
        left: 0
        img
            height: 400px
            width: 100vw
    &__wrap 
        position: relative
        display: flex
        align-items: stretch
        height: 100%
        @include sm 
            flex-direction: column-reverse
            align-items: center
            justify-self: flex-end
    &__col
        position: relative
        max-width: 600px
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
        @include sm 
            margin-bottom: 30px
        @include xxs
            margin-bottom: 15px
        @include xxxs
            max-width: 300px
    &__title
        margin-bottom: 50px
        color: $main-color
        font-family: $black-lato
        font-style: italic
        text-transform: uppercase
        font-size: 60px
        font-weight: 900
        line-height: 1
        align-self: flex-end
        white-space: nowrap
        @include xs
            margin-bottom: 40px 
        @include xxs
            margin-bottom: 30px
        @include xxxs
            margin-bottom: 20px
        &-1
            margin: 0 65px 5px 0
            @include xxxs
                margin: 0 35px 5px 0
        span
            color: $blue-color
        @include md 
            font-size: 48px
        @include xxs
            font-size: 36px
        @include xxxs
            font-size: 30px
    &__subtitle
        align-self: flex-end
        margin: 0 25px 10px 0
        font-size: 40px
        line-height: 110%
        text-transform: uppercase
        white-space: nowrap
        font-family: $bold-lato
        font-style: italic
        font-weight: 400
        @include md 
            font-size: 32px
        @include sm 
            max-width: none
            font-size: 30px
            text-align: center
        @include xxs 
            font-size: 24px
        @include xxxs
            margin: 0 15px 10px 0
            font-size: 20px
    &__offer
        margin-right: 25px
        align-self: flex-end
        @include md
            width: 100% 
            margin: 0
            align-self: center
        @include sm 
            display: flex
            flex-direction: column
            align-items: center        
    &__img 
        position: relative
        flex: 0 0 58%
        margin-left: -2%
        margin-right: -6%
        img
            height: 100%
            object-position: bottom
        @include lg 
            margin-left: -3%
            margin-right: -5%
        @include mdx
            height: 75%
            bottom: -25%
            margin-right: -2%
        @include sm 
            height: 100%
            bottom: 0
            flex: 0 1 auto
            margin-right: auto
            margin-left: auto
        @include xxs 
            margin-left: -16px
            width: calc( 100% + 32px )
            height: 520px
            img
                width: 520px
                object-position: bottom center
        @include xxxs
            display: flex
            justify-content: center
            width: calc( 100% + 32px )
            height: 400px
            img
                width: 400px
                object-position: bottom center

.promo-img
    &__bg
        height: 100%

.main-btn 
    position: relative
    align-self: flex-end
    margin-bottom: 30px
    display: inline-block
    position: relative
    transform: skew(-5deg) 
    overflow: hidden
    background: #0E789A
    transition: all .3s ease .3s
    &:hover .main-btn-item-1
        height: 0
    &:hover .main-btn-item-2
        height: 0
    &:hover .main-btn-item-3
        height: 0
    &:hover .main-btn-item-4
        height: 0
    &:hover .main-btn-item-5
        height: 0
    &:hover .main-link span
        transform: skew(0)
    &:hover
        transform: skew(0)
    .main-btn-item
        position: absolute
        background-color: rgba(19, 162, 209, 0.6)
        transition: all .3s ease
    .main-btn-item-1
        bottom: 0
        left: 18%
        height: 60%
        width: 35px
        z-index: 2
        @include xxs
            width: 25px
    .main-btn-item-2
        top: 0
        left: 28%
        height: 100%
        width: 35px
        z-index: 2
        @include xxs
            width: 25px
    .main-btn-item-3
        bottom: 0
        left: 45%
        height: 30%
        width: 35px
        z-index: 2
        @include xxs
            width: 25px
    .main-btn-item-4
        top: 0
        left: 65%
        height: 100%
        width: 70px
        z-index: 2
        @include xxs
            width: 50px
    .main-btn-item-5
        bottom: 0
        left: 94%
        height: 100%
        width: 20px
        z-index: 2
        @include xxs
            width: 10px
    .main-link
        position: relative
        display: flex
        justify-content: center
        line-height: 1
        font-size: 48px
        font-family: $bold-poppins
        font-style: normal
        font-weight: 700
        text-transform: uppercase
        white-space: nowrap
        color: $main-color
        padding: 40px 90px
        background: #172125
        background: linear-gradient(180deg, rgba(23,33,37,0.7301514355742297) 0%, rgba(14,120,154,0) 100%)
        transition: all .3s ease-in
        z-index: 4
        span
            z-index: 3
            transform: skew(5deg)
            transition: all .3s ease .3s
        @include md 
            font-size: 38px
        @include sm
            padding: 30px 60px  
            font-size: 34px
        @include xs 
            padding: 25px 50px
            font-size: 28px
        @include xxs 
            padding: 20px 40px
            font-size: 24px
        @include xxxs
            font-size: 20px
    @include md
        width: 100%
        
.offer
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    position: relative
    font-size: 24px
    p
        font-family: $reg-lato
        font-style: normal
        margin-top: 15px
        @include xs 
            margin-top: 12px
        @include xxs 
            margin-top: 10px
    @include mdx
        font-size: 20px
    @include sm 
        display: flex
        flex-direction: column
        align-items: center
    @include xs 
        font-size: 18px
    @include xxs 
        font-size: 16px

@keyframes arrow 
    0% 
        transform: translateY(5px)
    50% 
        transform: translateY(-5px)
    100% 
        transform: translateY(5px)

.arrow-offer
    display: inline-block
    position: relative
    height: 10px
    width: 20px
    animation: arrow 2s infinite ease-out
    @include xxs
        width: 15px

