.cookies-policy-banner
  background-image: url("../../images/cookie-bg.png")
  background-size: cover
  background-repeat: no-repeat
  background-position: 0 center
  height: 100%
  width: 100%
  bottom: 0
  height: auto
  overflow: hidden
  position: fixed
  width: 100%
  z-index: 200
  color: $text-cookie-color
  font-weight: 400
  &__inner-wrapper
    align-items: center
    display: flex
    height: 100%
    margin: 0 auto
    max-width: 1290px
    padding: 30px 15px
    +sm
      flex-direction: column
      align-items: center
      justify-content: center
  &__text-block
    display: flex
    max-width: 1000px
    width: 100%
    +sm
      flex-direction: column
    & .icon-wrap
      align-self: center
      & img
        max-height: 64px
    & .text 
      align-content: baseline
      display: flex
      flex-wrap: wrap
      margin: 0 30px 0 30px
      max-width: 746px
      +md
        margin: 0 15px 0 15px
      +sm
        justify-content: center
        margin: 0 auto
        max-width: 600px
      & .title
        font-size: 22px
        font-weight: 700
        margin-bottom: 10px
        +sm
          margin-top: 20px
          margin-bottom: 0
          text-align: center
      & p
        color: $text-cookie-color
        font-size: 15px
        margin: 5px 0
        font-weight: 400
        +md
          font-size: 14px
        +sm
          margin-bottom: 40px
          text-align: center
        +xs
          margin-bottom: 20px
        & a
          color: $text-cookie-color
          text-decoration: underline
          &:hover
            color: $btn-text-cookie-color
  &__button-block
    align-self: flex-end
    align-items: center
    display: flex
    flex-grow: 1
    height: 100%
    justify-content: flex-end
    transform: skew(-5deg)
    +sm
      align-self: center
    & .accept-cookie-button
      align-items: center
      background-image: url("../../images/btn-bg.png")
      background-size: cover
      background-repeat: no-repeat
      background-position: center center
      height: 100%
      width: 100%
      // background: #172125
      // background: linear-gradient(180deg, rgba(23,33,37,0.7301514355742297) 0%, rgba(14,120,154,0) 100%)
      color: $main-color
      cursor: pointer
      display: inline-flex
      font-size: 16px
      font-weight: 900
      height: 56px
      justify-content: center
      min-width: 150px
      padding: 0 15px
      text-transform: uppercase
      transition: all 0.3s
      user-select: none
      white-space: nowrap
      width: unset
      border: none
      position: relative
      & .check-icon
        background-color: $main-color
        display: inline-block
        height: 13px
        margin-left: 10px
        mask-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3E%3Cpath d='M20 4.095 17.986 2 7.554 12.848 2.014 7.05 0 9.145 7.554 17z'/%3E%3C/svg%3E")
        mask-position: center
        mask-repeat: no-repeat
        mask-size: 100%
        width: 15px
        transform: skew(5deg)

.button__content
  align-items: center
  display: flex
  flex-direction: row

