@media (max-width: 768px) {
  .section {
    margin-bottom: 25px;
  }
}

.cookies-policy-banner {
  background-image: url("../../images/cookie-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 center;
  height: 100%;
  width: 100%;
  bottom: 0;
  height: auto;
  overflow: hidden;
  position: fixed;
  width: 100%;
  z-index: 200;
  color: rgb(255, 255, 255);
  font-weight: 400;
}
.cookies-policy-banner__inner-wrapper {
  align-items: center;
  display: flex;
  height: 100%;
  margin: 0 auto;
  max-width: 1290px;
  padding: 30px 15px;
}
@media (max-width: 1024px) {
  .cookies-policy-banner__inner-wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.cookies-policy-banner__text-block {
  display: flex;
  max-width: 1000px;
  width: 100%;
}
@media (max-width: 1024px) {
  .cookies-policy-banner__text-block {
    flex-direction: column;
  }
}
.cookies-policy-banner__text-block .icon-wrap {
  align-self: center;
}
.cookies-policy-banner__text-block .icon-wrap img {
  max-height: 64px;
}
.cookies-policy-banner__text-block .text {
  align-content: baseline;
  display: flex;
  flex-wrap: wrap;
  margin: 0 30px 0 30px;
  max-width: 746px;
}
@media (max-width: 1280px) {
  .cookies-policy-banner__text-block .text {
    margin: 0 15px 0 15px;
  }
}
@media (max-width: 1024px) {
  .cookies-policy-banner__text-block .text {
    justify-content: center;
    margin: 0 auto;
    max-width: 600px;
  }
}
.cookies-policy-banner__text-block .text .title {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 10px;
}
@media (max-width: 1024px) {
  .cookies-policy-banner__text-block .text .title {
    margin-top: 20px;
    margin-bottom: 0;
    text-align: center;
  }
}
.cookies-policy-banner__text-block .text p {
  color: rgb(255, 255, 255);
  font-size: 15px;
  margin: 5px 0;
  font-weight: 400;
}
@media (max-width: 1280px) {
  .cookies-policy-banner__text-block .text p {
    font-size: 14px;
  }
}
@media (max-width: 1024px) {
  .cookies-policy-banner__text-block .text p {
    margin-bottom: 40px;
    text-align: center;
  }
}
@media (max-width: 768px) {
  .cookies-policy-banner__text-block .text p {
    margin-bottom: 20px;
  }
}
.cookies-policy-banner__text-block .text p a {
  color: rgb(255, 255, 255);
  text-decoration: underline;
}
.cookies-policy-banner__text-block .text p a:hover {
  color: #1E202F;
}
.cookies-policy-banner__button-block {
  align-self: flex-end;
  align-items: center;
  display: flex;
  flex-grow: 1;
  height: 100%;
  justify-content: flex-end;
  transform: skew(-5deg);
}
@media (max-width: 1024px) {
  .cookies-policy-banner__button-block {
    align-self: center;
  }
}
.cookies-policy-banner__button-block .accept-cookie-button {
  align-items: center;
  background-image: url("../../images/btn-bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 100%;
  width: 100%;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-size: 16px;
  font-weight: 900;
  height: 56px;
  justify-content: center;
  min-width: 150px;
  padding: 0 15px;
  text-transform: uppercase;
  transition: all 0.3s;
  user-select: none;
  white-space: nowrap;
  width: unset;
  border: none;
  position: relative;
}
.cookies-policy-banner__button-block .accept-cookie-button .check-icon {
  background-color: #fff;
  display: inline-block;
  height: 13px;
  margin-left: 10px;
  mask-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3E%3Cpath d='M20 4.095 17.986 2 7.554 12.848 2.014 7.05 0 9.145 7.554 17z'/%3E%3C/svg%3E");
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: 100%;
  width: 15px;
  transform: skew(5deg);
}

.button__content {
  align-items: center;
  display: flex;
  flex-direction: row;
}